.center-column {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.border-none {
  border: black solid 1px;
}


@media (orientation: landscape) {
	:root {
		--board-width: 95vh;
		--cell-width: calc(var(--board-width)/70);
	}
}	

@media (orientation: portrait) {
	:root {
		--board-width: 95vw;
		--cell-width: calc(var(--board-width)/70);
	}
}		

body.light-bright {
  -webkit-user-select: none; /* Safari 3.1+ */
  -moz-user-select: none; /* Firefox 2+ */
  -ms-user-select: none; /* IE 10+ */
  user-select: none; /* Standard syntax */
  font: black;
}

.btn-toolbar {
	display: flex;
	justify-content: center;
}

.btn {
	width: 15vw;
	font-size: 2vw;
}

.dropdown-menu, .dropdown-header {
	font-size: 2vw;
}

.buttons {
	margin: auto;
}

.bulb {
	border-radius: 50%;
	width: var(--cell-width);
	height: var(--cell-width);
}

#board {
	margin: auto;
	background: black;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-around;
	width: var(--board-width);
}

body.clubs {
  background: #6666cc;
}

h3.clubs {
  text-align: center;
}

.block {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background: #8c8cd9;
  color: #d9d9f2;
  border: solid #6666cc 1px;
  width: 16%;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  box-shadow: 5px 5px 10px #5353c6;
}

.club {
  font-size: 15px;
  padding: 5px;
}

.choices {
  margin-bottom: 20px;
  color: black;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-color: #DDD;
  border-radius: 10px;
  box-shadow: 5px 5px 10px #5353c6;
  padding: 5px;
  width: 250px;
}

.emoji {
  font-size: 20px;
  color: #222;
}

.hidden {
  visibility: hidden;
}

.notes {
  color: red;
  font-style: italic;
}

.selected-name {
  font-weight: bold;
}

#email-print {
  display: flex;
  justify-content: space-around;
}

#filters {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

#form {
  margin: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: flex-start;
}

#print-button {
  margin: 10px 0;
  width: 40%;
  border-radius: 5px;
}

#schedule {
  min-width: 850px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

@media print {
  body * {
    visibility: hidden;
  }

  #to-print, #to-print * {
    visibility: visible;
  }

  #to-print {
    font-size: 30px;
    position: absolute;
    left: 0;
    top: 0;
  }
}

